
import {defineComponent,getCurrentInstance,ref,toRefs,reactive,onMounted,watch,computed} from 'vue';
import {useStore} from 'vuex'
import {useRoute,useRouter} from 'vue-router'
export default defineComponent({
	name: 'Gooddetail',
	components: {},
	setup(props, context) {
		const router = useRouter();
		const store = useStore();
		const goback = ()=>{
			router.back();
		};
		const gotoCart = ()=>{
			router.push({name:'Cart',params:{}});
		}
		const cartLists = store.getters.getCartLists;
		const goodInfo = ref({id:'102',name:'木菠萝',desc:'热带水果',price:10,nums:1});
		const addCart = (data:any)=>{
			store.dispatch('cmAddCart',data);
		}
		const total = computed(()=>{
			let sum=0;
			let res = cartLists.findIndex((ele:any,index:any)=>{
				return ele.id==goodInfo.value.id;
			});
			if(res==-1){
				sum=0;
			}else{
				sum=cartLists[res].nums;
			}
			return sum;
		});
		const swipeImgs = ref([
			require("@/assets/ad1.jpg"), 
			require("@/assets/ad2.jpg"),
			require("@/assets/ad3.jpg"),
			require("@/assets/ad4.jpg"),
		]);
		return{gotoCart,total,addCart,goback,swipeImgs};
	}
	});
